body {
  background: #00d2ff;
}

main {
  display: grid;
  grid-template-columns: -webkit-min-content 2fr;
  grid-template-columns: min-content 2fr;

  background-color:#3d3d3d;
}

.sidebar {
  background-color: #252525;
  height: 100vh;

  color: #8c8c8c;
  border: 1px solid #000;
  border-radius: 5px;
}

.sidebar__links {
  padding: 1rem 0 0 0;
  margin: 0;
  display: flex;
  flex-direction: column;
  list-style-type: none;
  height: 100%;
  justify-content: space-between;
}

.sidebar-btns {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}

.sidebar__links .btn-drum {
  margin: 0;
  border: 1px solid #000;
  border-radius: 0;
}

.btn-drum {
  border: 2px solid rgba(0, 0, 0, 0.352);
  /* border-radius: 6px; */
  padding: 1rem 1.5rem;
  margin: 0.5rem 0;
  background-color: #242424;

}
.btn-drum .fa-play {
  color: green;
}

.btn-drum .fa-circle {
  color: #c4c4c4;
}

.btn-drum--active {
  
  background: #3d3d3d;
  text-shadow: #fff 0px 0px 2px;
}
btn-drum--sm {
  max-width: 25px;
}
.btn-drum.signal {
  padding: 1rem 1.39rem;
}


.title {
  color: #fff;
}

.slidecontainer {
  width: 100%;
  /* Width of the outside container */
}

.slider {
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
  -webkit-appearance: none;
  /* Override default CSS styles */
  -moz-appearance: none;
       appearance: none;
  width: 90%;
  /* Full-width */
  height: 25px;
  /* Specified height */
  background: #d3d3d3;
  /* Grey background */
  outline: none;
  /* Remove outline */
  opacity: 0.7;
  /* Set transparency (for mouse-over effects on hover) */
  /* 0.2 seconds transition on hover */
  transition: opacity 0.2s;
}

/* The slider handle (use -webkit- (Chrome, Opera, Safari, Edge) and -moz- (Firefox) to override default look) */

.slider::-webkit-slider-thumb {
  -webkit-appearance: none;
  /* Override default look */
  appearance: none;
  width: 25px;
  /* Set a specific slider handle width */
  height: 25px;
  /* Slider handle height */
  background: #4caf50;
  /* Green background */
  cursor: pointer;
  /* Cursor on hover */
}

.slider::-moz-range-thumb {
  width: 25px;
  /* Set a specific slider handle width */
  height: 25px;
  /* Slider handle height */
  background: #4caf50;
  /* Green background */
  cursor: pointer;
  /* Cursor on hover */
}

th {
  border-left: 2px solid grey;
  width: 25%;
  background-color: #000;
  padding: 0.5rem;
}

.App {
  background: linear-gradient(to top, #434343, #000000);
  /* width: 50%; */
  margin: 0 auto;
  /* border: 5px solid orange; */
}

/* .top {
    display: flex;
    justify-content: space-between;
} */

.btn-group {

  display: grid;
  grid-template-columns: repeat(2, 1fr);
  margin:  1rem;
  
}
.btn-group  button {
    color: #fff;
}


.btn-group .btn-drum {
  margin: 0;
}

.power-buttons {
  width: 25%;
}

.control-panel {
  display: grid;
  grid-template-columns: 1fr 2fr;
  /* padding-top: 1rem; */
}



.power-button {
  background: #536976;
  /* fallback for old browsers */
  /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(to top, #292e49, #536976);
  /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
  color: #fff;
  border: none;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
  padding: 1rem 2rem;
  margin: 1rem;
}

.\30,
.\31 {
  background: #fdc830;
  /* fallback for old browsers */
  /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(to top, #f37335, #fdc830);
  /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}

.\32,
.\33 {
  background: #06beb6;
  /* fallback for old browsers */
  /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(to top, #48b1bf, #06beb6);
  /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}

.\34,
.\35 {
  background: #ffb347;
  /* fallback for old browsers */
  /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(to top, #ffcc33, #ffb347);
  /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}

.\36,
.\37 {
  background: #1488cc;
  /* fallback for old browsers */
  /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(to top, #2b32b2, #1488cc);
  /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}

.\38 {
  background: #cb356b;
  /* fallback for old browsers */
  /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(to top, #bd3f32, #cb356b);
  /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}

.button-text {
  margin: 0;
  font-size: 0.8rem;
  color: white;
}

.button-text span {
  color: orange;
}

.master {
  margin: 0.5rem;
  border-top: 2px solid #fff;
  text-align: left;
  color: #fff;
}

@media only screen and (max-width: 768px) {
  body {
    background-color: red;
  }
}

.controls {
  padding-top: 0.5rem;
  background-color: #3d3d3d;
  border: 1px solid #000;
  border-radius: 5px;
}


.sample-bank {
  background-color: #3d3d3d;
  border: 1px solid rgba(0, 0, 0, 0.352);
  /* border-radius: 6px; */
  /* margin-bottom: .5rem; */
  display: flex;
  justify-content: space-between;
  align-items: center;
  position: relative;
  top: -4px;
  
}
.sample-bank:first-of-type  {
  border-top: none;
  
}
.sample-bank__label {
  margin: 0;
  padding: 1rem;
}


.select-samples {
  padding: 1rem;
  width: 100%;
  background-color: transparent;
  color: #fff;
  border: none;
  -webkit-appearance: none;
  -moz-appearance: none;
  -ms-appearance: none;
  -o-appearance: none;
  appearance: none;
  z-index: 10;
}

.sample-bank .fa-chevron-right {
  margin-right: 1rem;
  position: absolute;
  right: 0;
 
}
.drum-display {

  /* fallback for old browsers */
  /* background: -webkit-linear-gradient(to top, #434343, #000000); */
  /* Chrome 10-25, Safari 5.1-6 */
  /* background: linear-gradient(to top, #434343, #000000); */
  /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
  /* width: 75%; */
  /* margin: 0 1rem 0 0.15rem;
   */
  color: white;
  /* padding: 1rem 0 1rem 0.5rem; */
  margin: 1rem;
}

.drum-display__title {
  margin: 0;
  padding: 0.5rem;
  color: #8C2424;
  background: #0E0E0E;
}

.drum-display img {
  width: 100%;
  height: 250px;
}
.drum-buttons {
  display: grid;
  grid-template-columns: -webkit-min-content -webkit-min-content -webkit-min-content -webkit-min-content;
  grid-template-columns: min-content min-content min-content min-content;
  grid-gap: 1rem;
  gap: 1rem;
  justify-content: center;
  padding: 1rem;

}

.drum-button {
  border: 2px solid #000;
  border-radius: 6px;
  width: 150px;
  height: 175px;
 
  padding: 1rem 2rem;
  display:flex;
  justify-content: center;
  align-items: center;
}

.padBank {
  padding: 0 1rem 0 0;
 
 
  background: #686561;
  border: 1px solid #000;
  border-radius: 5px;
}
.nav {
  position: relative;
  background-color: #414141;

  box-shadow: 0 4px 5px 0 rgba(0,0,0,.9);
  z-index: 999;
}
.nav__links {
  display: flex;
  flex-direction: column;
 
  list-style: none;
  /* width: 100%; */
  margin: 0;
  justify-content: space-between;
  color: #8c8c8c;
}

.nav-logo {
  font-size: 1rem;
 
}
.nav-logo h1 {
  margin: 0;
}

.mid {
  display: flex;
  flex-wrap: wrap;
  /* flex-direction: column; */
}

@media (min-width: 800px) {
  .nav__links {
    flex-direction: row;
  }
  .nav-logo {
    font-size: 1.05rem;
    align-self: center;
  }

  .mid {
    flex-wrap: nowrap;
    flex-direction: row;
  }


}


.sound-select {
  width: 100%;
  margin: 0.5rem 0;
  padding: 0.5rem 1rem;
  background-color: #212121;
  color: #c4c4c4;
  border: 2px solid rgba(0, 0, 0, 0.352);
  /* border-radius: 6px; */
  width: 250px;
  -webkit-appearance: none;
  -moz-appearance: none;
  -ms-appearance: none;
  -o-appearance: none;
  appearance: none;
}

